___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: #fff;
  font-family: "Quicksand", sans-serif;
}

.navbar-nav li {
  padding: 0 20px;
  color: #a82d49;
  font-weight: bold;
}

.navbar-nav li a {
  color: #a82d49;
}

.meals-container {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  border: 2px solid #a82d49;
  border-radius: 10px;
  box-shadow: rgba(100, 100, 111, 0.2) 0 7px 29px 0;
}

.like-container {
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

.likebtn {
  padding-top: 3px;
}

.likebtn:hover {
  cursor: pointer;
}

.image {
  margin: 10px;
  width: auto;
  object-fit: cover;
  border-radius: 10px;
  overflow: hidden;
}

.meals {
  font-size: 18px;
  font-weight: normal;
  text-align: center;
}

.showLikes {
  position: absolute;
  padding-top: 2%;
  padding-left: 11.5%;
  font-weight: bold;
}

.modal-header {
  border: none;
}

.comment-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.comment,
.reservation {
  text-align: center;
  text-decoration: none;
  padding: 10px 20px;
  color: #fff;
}

.comment {
  background-color: #333;
}

.reservation {
  background-color: #a82d49;
  width: 100%;
}

.comment:hover {
  background-color: #a82d49;
}

.reservation:hover {
  background-color: #333;
}

.capitalise {
  text-transform: capitalize;
}

footer {
  border-top: 1px solid #ccc;
  margin-top: 24px;
  padding: 16px 0;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AACA;EACE,sBAAA;EACA,oCAAA;AAAF;;AAGA;EACE,eAAA;EACA,cAAA;EACA,iBAAA;AAAF;;AAGA;EACE,cAAA;AAAF;;AAGA;EACE,gBAAA;EACA,aAAA;EACA,sBAAA;EACA,yBAAA;EACA,mBAAA;EACA,iDAAA;AAAF;;AAGA;EACE,aAAA;EACA,aAAA;EACA,8BAAA;AAAF;;AAGA;EACE,gBAAA;AAAF;;AAGA;EACE,eAAA;AAAF;;AAGA;EACE,YAAA;EACA,WAAA;EACA,iBAAA;EACA,mBAAA;EACA,gBAAA;AAAF;;AAGA;EACE,eAAA;EACA,mBAAA;EACA,kBAAA;AAAF;;AAGA;EACE,kBAAA;EACA,eAAA;EACA,mBAAA;EACA,iBAAA;AAAF;;AAGA;EACE,YAAA;AAAF;;AAGA;EACE,UAAA;EACA,SAAA;EACA,qBAAA;AAAF;;AAGA;;EAEE,kBAAA;EACA,qBAAA;EACA,kBAAA;EACA,WAAA;AAAF;;AAGA;EACE,sBAAA;AAAF;;AAGA;EACE,yBAAA;EACA,WAAA;AAAF;;AAGA;EACE,yBAAA;AAAF;;AAGA;EACE,sBAAA;AAAF;;AAGA;EACE,0BAAA;AAAF;;AAGA;EACE,0BAAA;EACA,gBAAA;EACA,eAAA;AAAF","sourcesContent":["\r\nbody {\r\n  background-color: #fff;\r\n  font-family: 'Quicksand', sans-serif;\r\n}\r\n\r\n.navbar-nav li {\r\n  padding: 0 20px;\r\n  color: #a82d49;\r\n  font-weight: bold;\r\n}\r\n\r\n.navbar-nav li a {\r\n  color: #a82d49;\r\n}\r\n\r\n.meals-container {\r\n  margin-top: 40px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  border: 2px solid #a82d49;\r\n  border-radius: 10px;\r\n  box-shadow: rgba(100, 100, 111, 0.2) 0 7px 29px 0;\r\n}\r\n\r\n.like-container {\r\n  padding: 20px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n}\r\n\r\n.likebtn {\r\n  padding-top: 3px;\r\n}\r\n\r\n.likebtn:hover {\r\n  cursor: pointer;\r\n}\r\n\r\n.image {\r\n  margin: 10px;\r\n  width: auto;\r\n  object-fit: cover;\r\n  border-radius: 10px;\r\n  overflow: hidden;\r\n}\r\n\r\n.meals {\r\n  font-size: 18px;\r\n  font-weight: normal;\r\n  text-align: center;\r\n}\r\n\r\n.showLikes {\r\n  position: absolute;\r\n  padding-top: 2%;\r\n  padding-left: 11.5%;\r\n  font-weight: bold;\r\n}\r\n\r\n.modal-header {\r\n  border: none;\r\n}\r\n\r\n.comment-list {\r\n  padding: 0;\r\n  margin: 0;\r\n  list-style-type: none;\r\n}\r\n\r\n.comment,\r\n.reservation {\r\n  text-align: center